.intro-bk {
  background: #404040;
  width: 100vw;
  height: 100vh;
  z-index: 2;
  overflow: auto;
}

.intro-anim-container {
  margin-top: 25vh;
  height: 250px;
  width: 250px;
  border-radius: 100%;
}

.done {
  border-radius: 100%;
  height: 40px;
  width: 40px;
  top: 50%;
  left: 50%;
  z-index: 0;
  margin-left: -20px;
  margin-top: -20px;
  transform-origin: center;
  background: #3fb7fd;
  animation: done-anim 3.6s .8s forwards;
}

.intro-child {
  border-radius: 100%;
  background: #3fb7fd;
  height: 0;
  width: 0;
  top: 50%;
  left: 50%;
  margin-left: -15px;
  margin-top: -15px;
  transform-origin: center;
  z-index: -1;
}

.intro-child-anim0 {
  animation: collision1 1s 0.4s forwards;
  transform: translate(125px, 0);
}

.intro-child-anim1 {
  animation: collision2 1s 1.2s forwards;
  transform: translate(0, 125px);
}

.intro-child-anim2 {
  animation: collision3 1s 2s forwards;
  transform: translate(-125px, 0);
}

.intro-child-anim3 {
  animation: collision4 1s 2.8s forwards;
  transform: translate(0, -125px);
  background: #0ffc07;
}

.bar-anim1 {
  background: #f2f2f2;
  height: 30px;
  width: 9px;
  top: 50%;
  left: 50%;
  margin-left: -4.5px;
  margin-top: -15px;
  border-radius: 20px;
  opacity: 0;
  transform: translate(-15px, 6px) rotateZ(-45deg);
  animation: bar-anim 4s forwards;
}

.bar-anim2 {
  background: #f2f2f2;
  height: 50px;
  width: 9px;
  top: 50%;
  left: 50%;
  margin-left: -4.5px;
  margin-top: -25px;
  border-radius: 20px;
  opacity: 0;
  transform: translate(10px, 2px) rotateZ(45deg);
  animation: bar-anim 4s forwards;
}

.fade-out {
  animation: fade-out .7s forwards;
}

@media screen and (min-width: 768px) {
  .intro-anim-container {
    margin-top: 35vh;
  }
}

