@import '_vars';
//Intro anim
$wqt-anim1:.4s;
$wqt-anim2:1.2s;
$wqt-anim3:2s;
$wqt-anim4:2.8s;
.intro-bk {
  background: $c5;
  width: 100vw;
  height: 100vh;
  z-index: 2;
  overflow: auto;
}
.intro-anim-container {
  margin-top: 25vh;
  height: 250px;
  width: 250px;
  border-radius: 100%;
}
.done{
  $size:40px;
  border-radius: 100%;
  height:$size;
  width:$size;
  top:50%;
  left:50%;
  z-index: 0;
  margin-left:-($size/2);
  margin-top:-($size/2);
  transform-origin: center;
  background:#3fb7fd;
  animation: done-anim 3.6s .8s forwards;
}
$child-size:30px;
.intro-child{
  border-radius: 100%;
  background:#3fb7fd;
  height:0;
  width:0;
  top:50%;
  left:50%;
  margin-left:-($child-size/2);
  margin-top:-($child-size/2);
  transform-origin: center;
  z-index:-1;
}
.intro-child-anim0{
  animation:collision1 1s $wqt-anim1 forwards;
  transform:translate(125px,0);
}
.intro-child-anim1{
  animation:collision2 1s $wqt-anim2 forwards;
  transform:translate(0,125px);
}
.intro-child-anim2{
  animation:collision3 1s $wqt-anim3 forwards;
  transform:translate(-125px,0);
}
.intro-child-anim3{
  animation:collision4 1s $wqt-anim4 forwards;
  transform:translate(0,-125px);
  background: #0ffc07;
}
.bar-anim1{
  $h:30px;
  $w:9px;
  background: $c19;
  height:$h;
  width:$w;
  top:50%;
  left:50%;
  margin-left:-($w/2);
  margin-top:-($h/2);
  border-radius:20px;
  opacity: 0;
  transform: translate(-15px,6px) rotateZ(-45deg);
  animation:bar-anim 4s forwards;
}
.bar-anim2{
  $h:50px;
  $w:9px;
  background: $c19;
  height:$h;
  width:$w;
  top:50%;
  left:50%;
  margin-left:-($w/2);
  margin-top:-($h/2);
  border-radius:20px;
  opacity:0;
  transform: translate(10px,2px) rotateZ(45deg);
  animation:bar-anim 4s forwards;
}
@media screen and (min-width:768px) {
  .intro-anim-container {
    margin-top: 35vh;
  }
}
